<template>
  <div class="service">
    <div class="service-main">
      <p class="service-title">客服电话</p>
      <p class="service-tel">400-828-1029</p>
      <p>周一至周五:9:00-17.30</p>
      <div class="service-btn">
        <van-button style="background-color:#bfbfbf;" color="#fff;" @click="cancel">取消</van-button>
        <van-button type="info" class="service-btn-tel">
          <a href="tel:4008281029">拨打</a>
        </van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { Button } from 'vant'

export default {
  components: {
    [Button.name]: Button
  },
  methods: {
    cancel () {
      this.$router.push({
        path: '/me/about',
        query: {}
      })
    }
  }
}
</script>

<style scoped>
.service{
  font-size: 14px;
  height: 100vh;
  color: #666;
}
.service-main{
  margin: 50px auto;
  width: 70%;
  text-align: center;
  padding-top: 25px;
}
.service-title{
  font-size: 16px;
  font-weight: bold;
}
.service-tel{
  padding-top: 35px;
}
.service-btn{
  display: flex;
  justify-content:space-around;
}
.service-btn>button{
  width: 40%;
  font-size: 15px;
  border-radius: 3px;
}
.service-btn-tel a{
  display: inline-block;
  width: 100%;
  height: 100%;
  color:#fff;
}
</style>



